#app3 #square {
  border: 1px solid red;
  margin: 5vw;
  margin-top: 2vw;
  width: 10vw;
  height: 10vw;
}

@keyframes circle {
  0% {
  }
  100% {
    transform: translateX(10vw);
    border-radius: 50%;
    width: 20vw;
    height: 20vw;
  }
}

#app3 .active#square {
  animation: circle 1s linear forwards;
}
